.try {
	width: 50%;
	background-color: $color-sapphire;
	position: relative;
	z-index: 2;

	&::before {
		content: '';
		width: 100vw;
		height: 100%;
		background-color: $color-sapphire;
		position: absolute;
		top: 0;
		left: 1px;
		transform: translateX(-100%);
		z-index: -1;
	}
}

.try__wrapper {
	padding: 61px 170px 69px 0;

	h2 {
		font-family: 'Bebas Neue';
		font-size: 49px;
		line-height: 64px;
		letter-spacing: 0.03em;
		color: $color-default-white;
		margin: 0;
		margin-bottom: 28px;
	}

	ul {
		@include reset-list;
		display: flex;
		flex-wrap: wrap;
	}

	li {
		width: calc(100% / 2 - 15px);
		margin-right: 30px;
		margin-bottom: 30px;

		&:nth-child(2n) {
			margin-right: 0;
		}
	}

	input {
		@include inputStates;
		font-family: inherit;
		font-size: 14px;
		line-height: 16px;
		width: 100%;
		padding: 8px 0;
		color: darken($color-default-white, 20);
		background-color: $color-transparent;
		border: none;
		border-bottom: 1px solid $color-light-slate-grey;
	}

	button {
		border-width: 3px;
		font-size: 14px;
		line-height: 16px;
		padding: 15px;
	}
}

@media (max-width: $desktop-width - 1px) {
	.try {
		width: 100%;

		&::after {
			content: '';
			width: 100vw;
			height: 100%;
			background-color: $color-sapphire;
			position: absolute;
			top: 0;
			right: 1px;
			transform: translateX(100%);
			z-index: -1;
		}
	}

	.try__wrapper {
		padding: 70px 118px 70px 88px;

		h2 {
			font-size: 42px;
			line-height: 42px;
			margin-bottom: 31px;
		}

		li {
			margin-right: 60px;
			width: calc(100% / 2 - 30px);
		}

		input {
			padding: 7px 0;
		}
	}
}

@media (max-width: $tablet-width - 1px) {
	.try__wrapper {
		padding: 70px 0 71px 0;

		h2 {
			font-size: 32px;
			line-height: 32px;
			margin-bottom: 37px;
		}

		ul {
			flex-direction: column;
		}

		li {
			width: 100%;
			margin: 0;
			margin-bottom: 37px;
		}

		input {
			padding: 4px 0;
		}

		button {
			border-width: 3px;
		}
	}
}