.request {
	background-color: $color-torch-red;
	color: $color-default-white;
	padding: 83px 78px;
	width: 500px;
	height: 500px;

	p {
		font-weight: 400;
		font-size: 18px;
		margin: 0;
		margin-bottom: 50px;
	}

	a {
		padding: 24px;
	}
}

.request__main {
	display: flex;
	margin-bottom: 10px;

	h2 {
		width: 201px;
		font-family: 'Bebas Neue';
		font-size: 90px;
		line-height: 90px;
		letter-spacing: 0.03em;
		margin: 0;
	}

	dl {
		margin: 0;
		margin-left: 50px;
		margin-top: -4px;
	}

  dt {
		font-weight: 400;
		font-size: 18px;
		line-height: 27px;
	}
}

.request__time-point {
	margin: 0;
	margin-bottom: 8px;
	border-top: 1px solid $color-default-white;

	&--row {
		display: flex;
		align-items: center;
		width: 64px;
	}

	&:last-of-type {
		margin-bottom: 0;
	}
}

.request__time-day {
	font-weight: 700;
	font-size: 40px;
	line-height: 60px;
	margin-right: 7px;
}

.request__time-month {
	font-size: 14px;
	line-height: 21px;
}

.request__time-hours {
	font-weight: 700;
	font-size: 40px;
	line-height: 60px;
}

@media (max-width: $desktop-width - 1px) {
	.request {
		display: flex;
    flex-direction: column;
    justify-content: center;
		width: 43%;
		height: auto;
		padding: 51px 45px;

		p {
			font-size: 12px;
			line-height: 18px;
			margin-bottom: 28px;
		}

		a {
			font-size: 10px;
			line-height: 12px;
			padding: 12px;
			border-width: 2px;
		}
	}

	.request__main {
		margin: 0;
		display: flex;
		align-items: flex-start;

		h2 {
			font-size: 52px;
			line-height: 52px;
			width: auto;
		}

		dl {
			margin-left: 29px;
		}

		dt {
			font-size: 12px;
			line-height: 18px;
		}
	}

	.request__time-point {
		width: 56px;
		margin: 0;

		&--row {
			width: 42px;
		}
	}

	.request__time-day {
		font-size: 24px;
		line-height: 36px;
		margin-right: 3px;
	}

	.request__time-month {
		font-size: 10px;
		line-height: 14px;
		margin-top: -5px;
	}

	.request__time-hours {
		font-size: 24px;
		line-height: 36px;
		display: block;
    margin-top: -5px;
	}
}

@media (max-width: $tablet-width - 1px) {
	.request {
		width: 100%;
		max-width: 440px;
	}
}